<template>
  <qrcode-stream
      @decode="onDecode"
      @init="onInit"
      :camera="camera"
  />
</template>

<script>
import {QrcodeStream} from 'vue-qrcode-reader';

export default {
  name: 'Scanner.vue',
  components: {QrcodeStream},
  props: {
    apikey: String,
    allowedHosts: Array,
    onlyParse: Boolean,
    camera: {
      type: String,
      default: 'auto'
    }
  },
  methods: {
    isAllowedHost(host) {
      return !this.allowedHosts || !this.allowedHosts.length || this.allowedHosts.includes(host);
    },
    async onDecode(content) {
      let url;
      try {
        url = new URL(content);
        if (!this.isAllowedHost(url.host)) {
          this.$emit('error', {name: 'InvalidHost', message: 'This host is not allowed'});
          return;
        }
      } catch (error) {
        this.$emit('error', {name: 'InvalidURL', message: 'The QR-code does not contain a valid URL'});
        return;
      }
      if (this.onlyParse) {
        this.$emit('decode', url.pathname.split('/')[1])
      } else {
        try {
          const response = await fetch(url, {
            method: 'get',
            headers: new Headers({
              'apiKey': this.apikey,
              'Accept': 'application/json'
            })
          });
          if (!response.ok) {
            return this.$emit('error', response)
          }
          this.$emit('decode', response);
        } catch (error) {
          this.$emit('error', {name: 'FetchError', message: error.message});
        }
      }

    },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        this.$emit('error', error);
      }
    }
  }
};
</script>

<style lang="css">
.qrcode-stream-wrapper {
  position: relative;
  z-index: 0;
  width: 100%;
  height: 100%;
}

.qrcode-stream-overlay,
.tracking-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.qrcode-stream-camera,
.pause-frame {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
</style>
